import React from "react";
import { Typography } from "@material-ui/core";

const PriceOrSpecial = ({ price, special, currency }) => {
  return (
    <div className="flex__centerlize center">
      <Typography
        color={special ? "textSecondary" : "textPrimary"}
        style={{ textDecorationLine: special ? "line-through" : "" }}
        variant={special ? "caption" : "body2"}
      >
        {`${price} ${currency}`}
      </Typography>
      {special > 0 && (
        <Typography color="error" variant="body2">
          {`${special} ${currency}`}
        </Typography>
      )}
    </div>
  );
};

export default PriceOrSpecial;
