import React, { useState, useRef, useEffect, Fragment } from "react";
import {
  Typography,
  Chip,
  Box,
  Grid,
  IconButton,
  Tooltip,
} from "@material-ui/core";
import { useSelector, useDispatch } from "react-redux";
import * as actions from "../../store/actions";
import { useTranslation } from "react-i18next";
import MaterialTable from "material-table";
import IconTextNotice from "../../components/common/IconTextNotice";
import i18next from "../../i18n";
import Ttext from "../../components/common/Ttext";
import ErrorOutlineIcon from "@material-ui/icons/ErrorOutline";
import WidgetsRoundedIcon from "@material-ui/icons/WidgetsRounded";
import { arSA } from "date-fns/locale";
import SquareButton from "../../components/Buttons/SquareButton";
import { tableIcons } from "../../util/tableIcons";
import PriceOrSpecial from "../../components/Products/PriceOrSpecial";
import DoneRoundedIcon from "@material-ui/icons/DoneRounded";
import ClearRoundedIcon from "@material-ui/icons/ClearRounded";
import Input from "../../components/Inputs/Input";
import SelectInput from "../../components/Inputs/SelectInput";
import Checkbox from "../../components/Inputs/CheckboxLabel";
import { Link } from "react-router-dom";
import IosSwitch from "../../components/Inputs/IosSwitch";
import Globals from "../../util/globals";
import EditRoundedIcon from "@material-ui/icons/EditRounded";
import DeleteRoundedIcon from "@material-ui/icons/DeleteRounded";
import LocalOfferRoundedIcon from "@material-ui/icons/LocalOfferRounded";
import GeneralDialog from "../../components/common/GeneralDialog";
import PageError from "../../components/common/PageError";

const tableHeaders = [
  {
    title: <Ttext ns="common" text="number" />,
    field: "product_id",
    width: 1,
  },
  {
    title: <Ttext ns="common" text="image" />,
    field: "image",
    width: 1,
    render: (row) => {
      return (
        <Link to={`/products/${row.product_id}`}>
          <div className="border-light p">
            <img
              alt={row.title}
              src={`${Globals.server}/${row.image[0]}`}
              width={50}
            />
          </div>
        </Link>
      );
    },
  },
  {
    title: <Ttext ns="common" text="name" />,
    field: "title",
  },
  {
    title: <Ttext ns="common" text="price" />,
    field: "price",
    render: (row) => (
      <PriceOrSpecial
        price={row.price}
        special={row.special}
        currency={row.currency}
      />
    ),
  },
  {
    title: <Ttext ns="common" text="quantity" />,
    field: "quantity",
  },
  {
    title: <Ttext ns="common" text="categories" />,
    field: "categories",
    render: (row) => (
      <div className="flex__centerlize">
        {row.categories.length ? (
          row.categories.map((cat) => (
            <Chip
              key={cat.id}
              className="xm"
              size="small"
              label={cat.title}
              icon={cat.status > 0 ? <DoneRoundedIcon /> : <ClearRoundedIcon />}
              color={cat.status ? "primary" : "secondary"}
              classes={{ label: "text-smaller" }}
            />
          ))
        ) : (
          <Ttext ns="products" text="nothing" />
        )}
      </div>
    ),
    width: 1,
    sorting: false,
  },
  {
    title: <Ttext ns="common" text="filters" />,
    field: "filters",
    render: (row) => (
      <div className="flex__centerlize">
        {row.filters.length ? (
          row.filters.map((filter) => (
            <Chip
              key={filter.id}
              className="xp"
              size="small"
              label={filter.title}
              icon={
                filter.status > 0 ? <DoneRoundedIcon /> : <ClearRoundedIcon />
              }
              color={filter.status ? "primary" : "secondary"}
              classes={{ label: "text-smaller" }}
            />
          ))
        ) : (
          <Ttext ns="products" text="nothing" />
        )}
      </div>
    ),
    width: 1,
    sorting: false,
  },
  {
    title: <Ttext ns="common" text="points" />,
    field: "points",
    width: 1,
  },
  {
    title: <Ttext ns="products" text="curr_coupon" />,
    field: "coupon",
    render: (row) =>
      row.coupon ? (
        <Typography variant="caption">{row.coupon}</Typography>
      ) : (
        <Ttext ns="products" text="nothing" />
      ),
    width: 1,
    sorting: false,
  },
  {
    title: <Ttext ns="common" text="views" />,
    field: "view",
    width: 1,
  },
  {
    title: <Ttext ns="common" text="sold" />,
    field: "sold",
    width: 1,
  },
  // {
  //   title: <Ttext ns="common" text="available_at" />,
  //   field: "available_at",
  //   render: (row) =>
  //     format(new Date(row.available_at), "yyyy MMMM dd hh:mm:ss aaaa", {
  //       locale: i18next.language === "ar" ? arSA : "",
  //     }),
  // },
];

const initialState = {
  q: "",
  category: [],
  filter: [],
  special: false,
};
let timeout;
let initialized = false;

const Products = ({ location }) => {
  const [state, setState] = useState(initialState);
  const [deleteDialog, setDeleteDialog] = useState({
    open: false,
    selected: null,
  });
  const products = useSelector((state) => state.products);
  const [tableSize, setTableSize] = useState(10);
  const dispatch = useDispatch();
  const { t } = useTranslation();
  const tableRef = useRef();

  const handleQuery = (value, validity, name) => {
    setState((prev) => {
      return {
        ...prev,
        [name]: value,
      };
    });
  };

  useEffect(() => {
    if (initialized && state !== initialState) {
      timeout = setTimeout(() => {
        updateTable();
      }, 500);
    }
    return () => clearTimeout(timeout);
  }, [state]);

  const updateTable = () =>
    tableRef.current && tableRef.current.onQueryChange();

  const getSelectOptions = (options, isCat) => {
    if (!options) {
      return [];
    }

    const ops = options.map((op) => {
      return {
        text: op.title,
        value: isCat ? op.category_id : op.filter_id,
      };
    });

    return ops;
  };

  const handleSwitchState = async (data) => {
    const id = data.product_id;
    const status = data.status > 0 ? 0 : 1;
    await dispatch(actions.switchProductStatus(id, status));
    const newData = tableRef.current.state.renderData.map((dat) => {
      if (dat.product_id === id) {
        return {
          ...dat,
          status: status,
        };
      }
      return dat;
    });
    tableRef.current.setState({ renderData: newData });
  };

  const handleTriggerDeleteDialog = (selected) => {
    setDeleteDialog({
      open: !deleteDialog.open,
      selected: selected,
    });
  };

  const handleDeleteProduct = async () => {
    const id = deleteDialog.selected.product_id;
    const deletedId = await dispatch(actions.deleteProduct(id));
    const newData = tableRef.current.state.renderData.filter(
      (dat) => dat.product_id !== deletedId
    );
    tableRef.current.setState({ renderData: newData });
    handleTriggerDeleteDialog();
  };

  const arrangeQuery = (_query) => {
    const query = {
      category: state.category.join(","),
      filter: state.filter.join(","),
      special: state.special,
      q: state.q,
      page: _query.page,
      perPage: _query.pageSize,
      sort: _query.orderBy ? _query.orderBy.field : "date_added",
      direction: _query.orderDirection,
    };
    return query;
  };

  return (
    <Fragment>
      <Box>
        <Typography variant="h4" className="py">
          {t("products:products_list")}
        </Typography>
        <Grid container spacing={2} alignItems="center" className="py">
          <Grid item sm={4} xs={12}>
            <Input
              label={t("common:searchhhh")}
              getResult={handleQuery}
              name="q"
              initialValue={state.q}
            />
          </Grid>
          <Grid item sm={2} xs={4}>
            <SelectInput
              options={getSelectOptions(products.products.categories, true)}
              name="category"
              label={t("products:choose_category")}
              initialValue={state.category}
              getResult={handleQuery}
              multiple
            />
          </Grid>
          <Grid item sm={2} xs={4}>
            <SelectInput
              options={getSelectOptions(products.products.filters)}
              label={t("products:choose_filter")}
              name="filter"
              initialValue={state.filter}
              getResult={handleQuery}
              multiple
            />
          </Grid>
          <Grid item sm={2} xs={4}>
            <Checkbox
              label={t("common:specials")}
              name="special"
              state={state.special}
              onChange={handleQuery}
            />
          </Grid>
          <Grid item sm={2} xs={12}>
            <Link to={`${location.pathname}/add`}>
              <SquareButton
                title={t("products:new_product")}
                wIcon={
                  <WidgetsRoundedIcon className="text-light" fontSize="large" />
                }
                py={0.6}
                px={0.8}
                type="primary"
              />
            </Link>
          </Grid>
        </Grid>
      </Box>
      {!products.loading && products.errors ? (
        <PageError
          text={products.errors.message || t("common:uncommon_err")}
          onTryAgain={() => dispatch(actions.getProducts())}
        />
      ) : (
        <MaterialTable
          icons={tableIcons}
          columns={tableHeaders}
          tableRef={tableRef}
          data={async (_query) => {
            const myquery = arrangeQuery(_query);

            try {
              const res = await dispatch(actions.getProducts(myquery));
              const data = res.data;
              initialized = true;
              return {
                data: data.products,
                page: data.pagination.currentPage - 1,
                totalCount: data.pagination.totalCount,
              };
            } catch (err) {
              tableRef.current &&
                tableRef.current.setState({ isLoading: false });
              let text = t("common:uncommon_err");
              if (err.message) {
                text = err.message;
              }

              const error = {
                message: (
                  <IconTextNotice
                    Icon={ErrorOutlineIcon}
                    text={text}
                    color="error"
                  />
                ),
              };
              throw error;
            }
          }}
          components={{
            Toolbar: (props) => " ",
            Actions: (props) => {
              const prod = props.data;
              return (
                <>
                  <Link
                    to={{
                      pathname: `${location.pathname}/${prod.product_id}`,
                      hash: "specials",
                    }}
                  >
                    <Tooltip title={t("common:specials")}>
                      <IconButton>
                        <LocalOfferRoundedIcon color="primary" />
                      </IconButton>
                    </Tooltip>
                  </Link>
                  <Tooltip title={t("common:edit")}>
                    <Link to={`${location.pathname}/edit/${prod.product_id}`}>
                      <IconButton>
                        <EditRoundedIcon color="secondary" />
                      </IconButton>
                    </Link>
                  </Tooltip>
                  <Tooltip title={t("common:delete")}>
                    <IconButton onClick={() => handleTriggerDeleteDialog(prod)}>
                      <DeleteRoundedIcon color="error" />
                    </IconButton>
                  </Tooltip>
                  <IosSwitch
                    state={prod.status}
                    onChange={() => handleSwitchState(props.data)}
                    name="status"
                    loading={products.itemLoading}
                  />
                </>
              );
            },
          }}
          actions={[
            {
              icon: () => null,
              onClick: () => null,
              hidden: true,
            },
          ]}
          options={{
            detailPanelColumnAlignment: "right",
            actionsColumnIndex: -1,
            draggable: false,
            emptyRowsWhenPaging: false,
            search: false,
            pageSize: tableSize,
            pageSizeOptions: [5, 10, 20, 50],
            thirdSortClick: false,
            debounceInterval: 500,
            showTitle: false,
          }}
          onChangeRowsPerPage={(size) => setTableSize(size)}
          localization={{
            header: {
              actions: "",
            },
            body: {
              emptyDataSourceMessage: products.loading
                ? t("common:loadingggg")
                : !products.loading &&
                  !products.errors && (
                    <IconTextNotice
                      Icon={WidgetsRoundedIcon}
                      text={t("products:no_products")}
                    />
                  ),
              dateTimePickerLocalization: i18next.language === "ar" ? arSA : "",
            },
            pagination: {
              labelDisplayedRows:
                "{from}-{to} " + t("common:from") + " {count}",
              labelRowsSelect: t("common:perpage"),
            },
          }}
          isLoading={products.loading}
        />
      )}

      <GeneralDialog
        dialogOpen={deleteDialog.open}
        confirmText={t("common:confirm")}
        title={t("products:confirm_delete")}
        subtitle={t("products:confirm_delete_info")}
        onCancel={() => handleTriggerDeleteDialog()}
        onConfirm={handleDeleteProduct}
        loading={products.itemLoading}
        disableBackdropClick
      />
    </Fragment>
  );
};

export default Products;
